<template lang="pug">
.counter-component
  .counter-btn(
    @click="minus"
  ) -
  .counter-show
    input(
      type="text",
      v-model="number",
      @keyup="fixNumber"
    )
  .counter-btn(
    @click="add"
  ) +
</template>
<script>
export default {
  props: {
    max: {
      type: Number,
      default: 5
    },
    min: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      number: this.min
    }
  },
  watch: {
    number () {
      this.$emit('on-change', this.number)
    }
  },
  methods: {
    fixNumber () {
      let fix
      if (typeof this.number === 'string') {
        fix = Number(this.number.replace(/\D/g), '')
      } else {
        fix = this.number
      }
      if (fix > this.max || fix < this.min) {
        fix = this.min
      }
      this.number = fix
    },
    minus () {
      if (this.number <= this.min) {
        return
      }
      this.number--
    },
    add () {
      if (this.number >= this.max) {
        return
      }
      this.number++
    }
  }
}
</script>
<style lang="scss" scoped>
.counter-component {
  position: relative;
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
  .counter-show {
    float: left;
    input {
      border: none;
      border-top: 1px solid #e3e3e3;
      border-bottom: 1px solid #e3e3e3;
      height: 25px;
      line-height: 25px;
      width: 30px;
      outline: none;
      text-align: center;
    }
  }
  .counter-btn {
    border: 1px solid #e3e3e3;
    float: left;
    height: 25px;
    line-height: 25px;
    width: 25px;
    text-align: center;
    cursor: pointer;
    &:hover {
      border-color: #4fc08d;
      background: #4fc08d;
      color: #fff;
    }
  }
}
</style>



