<template lang="pug">
.layout(
  @click="clearComponentStatus"
)
  .app-head
    .app-head-inner
      router-link(
        :to="{ path: '/' }"
      )
        img(
          src="../../assets/logo.png"
        )
      .head-nav
        ul.nav-list
          li {{ username }}
          li.nav-pile(
            v-show="username!==''"
          ) |
          li(
            v-show="username!==''",
            @click="quit"
          ) 
          li(
            v-show="username===''",
            @click="logClick"
          ) 
          li.nav-pile |
          li(
            v-show="username===''",
            @click="regClick"
          ) 
          li.nav-pile(
            v-show="username===''"
          ) |
          li(
            @click="aboutClick"
          ) 
  .container
    keep-alive
      router-view
  .app-foot
    p  2018 fishenal MIT
  my-dialog(
    :is-show="isShowAboutDialog",
    @on-close="closeDialog('isShowAboutDialog')"
  )
    p 
  my-dialog(
    :is-show="isShowLogDialog",
    @on-close="closeDialog('isShowLogDialog')"
  )
    log-form(
      @has-log="onSuccessLog"
    )
  my-dialog(
    :is-show="isShowRegDialog",
    @on-close="closeDialog('isShowRegDialog')"
  )
    reg-form
</template>

<script>
import MyDialog from '@/components/base/dialog'
import LogForm from '@/components/base/logForm'
import RegForm from '@/components/base/regForm'
import { eventBus } from '../../eventBus'
export default {
  data () {
    return {
      isShowAboutDialog: false,
      isShowLogDialog: false,
      isShowRegDialog: false,
      username: ''
    }
  },
  components: {
    'my-dialog': MyDialog,
    'log-form': LogForm,
    'reg-form': RegForm
  },
  methods: {
    quit () {},
    logClick () {
      this.isShowLogDialog = true
    },
    regClick () {
      this.isShowRegDialog = true
    },
    aboutClick () {
      this.isShowAboutDialog = true
    },
    closeDialog (type) {
      this[type] = false
    },
    onSuccessLog (data) {
      this.closeDialog('isShowLogDialog')
      this.username = data.username
    },
    clearComponentStatus () {
      eventBus.$emit('reset-status')
      // console.log(el)
    }
  }
}
</script>



