<template lang="pug">
  .slide-show(
    v-if="slides[nowIndex]",
    @mouseover="clearInv",
    @mouseout="runInv"
  )
    .slide-img
      a
        transition(name="slide-trans")
          img(
            :src="slides[nowIndex].src"
          )
        transition(name="slide-trans-old")
          img(
            v-show="!isShow",
            :src="slides[nowIndex].src"
          )
    h2 {{ slides[nowIndex].title }}
    ul.slide-pages
      li(
        @click="goto(preIndex)"
      ) &lt;
      li(
        v-for="(item, index) in slides",
        @click="goto(index)"
      )
        a(
          :class="{on: index === nowIndex}"
        ) {{ index + 1}}
      li(
        @click="goto(nextIndex)"
      ) &gt;
</template>
<script>
export default {
  props: {
    slides: {
      type: Array,
      default: function () {
        return []
      }
    },
    inv: {
      type: Number,
      default: 2000
    }
  },
  data () {
    return {
      nowIndex: 0,
      isShow: true,
      invId: null,
      gotoInv: null
    }
  },
  computed: {
    preIndex () {
      if (this.nowIndex === 0) {
        return this.slides.length - 1
      } else {
        return this.nowIndex - 1
      }
    },
    nextIndex () {
      if (this.nowIndex === this.slides.length - 1) {
        return 0
      } else {
        return this.nowIndex + 1
      }
    }
  },
  mounted () {
    this.runInv()
  },
  methods: {
    clearInv () {
      clearInterval(this.invId)
      clearInterval(this.gotoInv)
      this.invId = null
      this.gotoInv = null
    },
    runInv () {
      this.invId = setInterval(() => {
        this.goto(this.nextIndex)
      }, this.inv)
    },
    goto (index) {
      this.isShow = false
      this.gotoInv = setTimeout(() => {
        this.isShow = true
        this.nowIndex = index
      }, 10)
    }
  }
}
</script>
<style lang="scss" scoped>
.slide-trans-enter-active {
  transition: all .5s;
}
.slide-trans-enter {
  transform: translateX(900px);
}
.slide-trans-old-leave-active {
  transition: all .5s;
  transform: translateX(-900px);
}
.slide-show {
  position: relative;
  margin: 15px 15px 15px 0;
  width: 700px;
  height: 386px;
  overflow: hidden;
  h2 {
    position: absolute;
    width: 100%;
    height: 100%;
    color: #fff;
    background: #000;
    opacity: .5;
    bottom: 0;
    height: 30px;
    text-align: left;
    padding-left: 15px;
    line-height: 30px;
  }
  .slide-img {
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
    }
  }
  .slide-pages {
    position: absolute;
    bottom: 10px;
    right: 15px;
    li {
      display: inline-block;
      padding: 0 10px;
      cursor: pointer;
      color: #fff;
      .on {
        text-decoration: underline;
      }
    }
  }
}
</style>



