<template lang="pug">
.order-wrap
  h3 
  .order-list-choose
    .order-list-option 
      v-selection(
        :selection="products",
        @on-change="productChange"
      )
    .order-list-option 
      v-date-picker(
        @on-change="getStartDate"
      )
    .order-list-option 
      input.order-query(
        type="text",
        v-model.lazy="query"
      )
  .order-list-table
    table
      tr
        th(
          v-for="head in tableHeads",
          @click="changeOrderType(head)",
          :class="{active: head.active}"
        ) {{ head.label }} {{ head.key }}
      tr(
        v-for="item in tableData",
        :key="item.orderId"
      )
        td(
          v-for="head in tableHeads"
        ) {{ item[head.key] }}
    .table-pagenation
</template>
<script>
import VSelection from '@/components/base/select'
import VDatePicker from '@/components/base/datePicker'
import axios from 'axios'
// import _ from 'lodash'
export default {
  data () {
    return {
      query: '',
      productId: 0,
      startDate: '',
      endDate: '',
      products: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 3
        }
      ],
      tableHeads: [
        {
          label: '',
          key: 'orderId'
        },
        {
          label: '',
          key: 'product'
        },
        {
          label: '',
          key: 'version'
        },
        {
          label: '',
          key: 'period'
        },
        {
          label: '',
          key: 'date'
        },
        {
          label: '',
          key: 'buyNum'
        },
        {
          label: '',
          key: 'amount'
        }
      ],
      tableData: [],
      pageSize: 10,
      offset: 0
    }
  },
  components: {
    'v-selection': VSelection,
    'v-date-picker': VDatePicker
  },
  methods: {
    pageChange () {},
    productChange () {},
    changeOrderType () {},
    getStartDate () {}
  },
  mounted () {
    axios.post('api/getTableData')
      .then((res) => {
        console.log(res)
        this.tableData = res.data.list
      })
      .catch((error) => {
        console.log(error)
      })
  }
}
</script>
<style lang="scss" scoped>
.order-wrap {
  width: 1000px;
  min-height: 800px;
  margin: 20px auto;
  overflow: hidden;
}
.order-wrap h3 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}
.order-query {
  height: 25px;
  line-height: 25px;
  border: 1px solid #e3e3e3;
  outline: none;
  text-indent: 10px;
}
.order-list-option {
  display: inline-block;
  padding-left: 15px;
}
.order-list-option:first-child {
  padding-left: 0;
}
.order-list-table {
  margin-top: 20px;
}
.order-list-table table {
  width: 100%;
  background: #fff;
}
.order-list-table td,
.order-list-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 10px 0;
}
.order-list-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
  cursor: pointer;
}
.order-list-table th.active {
  background: #35495e;
}
.table-pagenation {
  padding: 15px;
  text-align: right;
}
</style>



