<template lang="pug">
.pay
  this-dialog(
    :is-show="isShowCheckDialog",
    @on-close="checkStatus"
  ) 
    .button(
      @click="checkStatus(true)"
    ) 
    .button(
      @click="checkStatus(false)"
    ) 
  this-dialog(
    :is-show="isShowSuccessDialog",
    @on-close="toOrderList"
  ) 
  this-dialog(
    :is-show="isShowFailDialog",
    @on-close="toOrderList"
  ) 
</template>
<script>
import Dialog from './dialog'
import axios from 'axios'
export default {
  components: {
    'this-dialog': Dialog
  },
  props: {
    isShowCheckDialog: {
      type: Boolean,
      default: false
    },
    orderId: {
      type: [String, Number]
    }
  },
  data () {
    return {
      isShowSuccessDialog: false,
      isShowFailDialog: false,
      isPay: false
    }
  },
  methods: {
    checkStatus (status) {
      if (!status) {
        this.isShowFailDialog = true
        this.$emit('on-close-check-dialog')
        this.isPay = false
        return
      }
      axios.post('/api/checkOrder', {
        orderId: this.orderId
      })
        .then((res) => {
          this.isPay = true
          this.isShowSuccessDialog = true
          this.$emit('on-close-check-dialog')
        })
        .catch((err) => {
          console.log(err)
          this.isShowFailDialog = true
          this.$emit('on-close-check-dialog')
          this.isPay = false
        })
    },
    toOrderList () {
      if (this.isPay) {
        this.$router.push({path: '/orderList'})
      }
      this.isShowSuccessDialog = false
      this.isShowFailDialog = false
    }
  }
}
</script>
<style lang="scss" scoped>
.button {
  margin: 0 10px;
}
</style>



