<template lang="pug">
.chooser-component
  ul.chooser-list
    li(
      v-for="(item, index) in selections",
      @click="choosenSelection(index)",
      :title="item.label",
      :class="{active:index === nowIndex}"
    ) {{ item.label }}
</template>
<script>
export default {
  props: {
    selections: {
      type: Array,
      default: function () {
        return [{
          label: 'test',
          value: 0
        }]
      }
    }
  },
  data () {
    return {
      nowIndex: 0
    }
  },
  methods: {
    choosenSelection (index) {
      this.nowIndex = index
      this.$emit('on-change', this.selections[index])
    }
  }
}
</script>
<style lang="scss" scoped>
.chooser-component {
  position: relative;
  display: inline-block;
  li{
    display: inline-block;
    border: 1px solid #e3e3e3;
    height: 25px;
    line-height: 25px;
    padding: 0 8px;
    margin-right: 5px;
    border-radius: 3px;
    text-align: center;
    cursor: pointer;
    &.active {
      border-color: #4fc08d;
      background: #4fc08d;
      color: #fff;
    }
  }
}
</style>



