<template lang="pug">
.selection-component
  .selection-show(
    @click="toggleDrop"
  )
    span {{ selections[nowIndex].label }}
    .arrow
  .selection-list(v-if="isDrop")
    ul
      li(
        v-for="(item, index) in selections",
        @click="chooseSelection(index)"
      ) {{ item.label }}
</template>
<script>
import { eventBus } from '../../eventBus'
export default {
  props: {
    selections: {
      type: Array,
      default: function () {
        return [{
          label: 'test',
          value: 0
        }]
      }
    }
  },
  data () {
    return {
      isDrop: false,
      nowIndex: 0
    }
  },
  mounted () {
    eventBus.$on('reset-status', () => {
      this.isDrop = false
    })
  },
  methods: {
    toggleDrop (event) {
      event.stopPropagation()
      this.isDrop = !this.isDrop
      if (!this.isDrop) {
        event.$emit('reset-status')
      }
    },
    chooseSelection (index) {
      this.nowIndex = index
      eventBus.$emit('reset-status')
      this.$emit('on-change', this.selections[this.nowIndex])
    }
  }
}
</script>
<style lang="scss" scoped>
.selection-component {
  position: relative;
  display: inline-block;
  .selection-show {
    border: 1px solid #e3e3e3;
    padding: 0 20px 0 10px;
    display: inline-block;
    position: relative;
    cursor: pointer;
    height: 25px;
    line-height: 25px;
    border-radius: 3px;
    background: #fff;
    .arrow {
      display: inline-block;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-top: 5px solid #e3e3e3;
      width: 0;
      height: 0;
      margin-top: -1px;
      margin-left: 6px;
      margin-right: -14px;
      vertical-align: middle;
    }
  }
  .selection-list {
    display: inline-block;
    position: absolute;
    left: 0;
    top: 25px;
    width: 100%;
    background: #fff;
    border-top: 1px solid #e3e3e3;
    border-bottom: 1px solid #e3e3e3;
    z-index: 5;
    li {
      padding: 5px 15px 5px 10px;
      border-left: 1px solid #e3e3e3;
      border-right: 1px solid #e3e3e3;
      cursor: pointer;
      background: #fff;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      &:hover {
        background: #e3e3e3;
      }
    }
  }
}
</style>



