<template lang="pug">
  .dialog-wrap
    .dialog-cover(
      v-show="isShow",
      @click="closeMyself"
    )
    transition(name="drop")
      .dialog-content(
        v-show="isShow"
      )
        p.dialog-close(
          @click="closeMyself"
        ) x
        slot empty
</template>
<script>
export default {
  props: {
    isShow: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {}
  },
  methods: {
    closeMyself () {
      this.$emit('on-close')
    }
  }
}
</script>
<style lang="scss" scoped>
.drop-enter-active {
  transition: all .5s ease;
}
.drop-leave-active {
  transition: all .3s ease;
}
.drop-enter {
  transform: translateY(-500px);
}
.drop-leave-active {
  transform: translateY(-500px);
}

.dialog-wrap {
  position: fixed;
  width: 100%;
  height: 100%;
  .dialog-cover {
    background: #000;
    opacity: .3;
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .dialog-content {
    width: 50%;
    position: fixed;
    overflow: auto;
    background: #fff;
    top: 20%;
    left: 50%;
    margin-left: -25%;
    z-index: 10;
    border-radius: 10px;
    padding: 2%;
    line-height: 1.6;
    .dialog-close {
      position: absolute;
      right: 5px;
      top: 5px;
      width: 20px;
      height: 20px;
      text-align: center;
      cursor: pointer;
    }
    .dialog-close:hover {
      color: #4fc08d;
    }
  }
}
</style>



