<template lang="pug">
.sales-board
  .sales-board-intro
    h2 
    p 
  .sales-board-form
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right
        v-counter(
          @on-change="onParamChange('buyNum', $event)"
        )
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right
        v-selection(
          :selections="buyTypes",
          @on-change="onParamChange('buyType', $event)"
        )
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right
        v-chooser(
          :selections="periodList",
          @on-change="onParamChange('period', $event)"
        )
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right
        v-mul-chooser(
          :selections="versionList",
          @on-change="onParamChange('version', $event)"
        )
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right {{ price }}
    .sales-board-line
      .sales-board-line-left &nbsp;
      .sales-board-line-right
        .button(
          @click="showPayDialog"
        ) 
  .sales-board-des
    h2 
    p 
    h3 
    ul
      li 
      li 
      li 
      li 
      li 
    h3 
    ul
      li 
      li 
      li 
      li 
      li 
  my-dialog(
    :is-show="isShowPayDialog",
    @on-close="hidePayDialog"
  )
    table.buy-dialog-table
      tr
        th 
        th 
        th 
        th 
        th 
      tr
        td {{ buyNum }}
        td {{ buyType.label }}
        td {{ period.label }}
        td
          span(
            v-for="item in versions"
          ) {{ item.label }}
        td {{ price }}
    h3.buy-dialog-title 
    bank-chooser(
      @on-change="onChangeBanks"
    )
    .button.buy-dialog-btn(
      @click="confirmBuy"
    ) 
  my-dialog(
    :is-show="isShowErrDialog",
    @on-close="hideErrDialog"
  ) 
  check-order(
    :is-show-check-dialog="isShowCheckOrder",
    :order-id="orderId",
    @on-close-check-dialog="hideCheckOrder"
  )
</template>
<script>
import _ from 'lodash'
import axios from 'axios'
import VSelection from '@/components/base/select'
import VCounter from '@/components/base/counter'
import VChooser from '@/components/base/chooser'
import VMulChooser from '@/components/base/mulChooser'
import MyDialog from '@/components/base/dialog'
import BankChooser from '@/components/base/bankChooser'
import CheckOrder from '@/components/base/checkOrder'
export default {
  data () {
    return {
      buyNum: 0,
      buyType: {},
      versions: [],
      period: {},
      price: 0,
      versionList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      periodList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      buyTypes: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      isShowPayDialog: false,
      bankId: null,
      orderId: null,
      isShowCheckOrder: false,
      isShowErrDialog: false
    }
  },
  components: {
    'v-selection': VSelection,
    'v-counter': VCounter,
    'v-chooser': VChooser,
    'v-mul-chooser': VMulChooser,
    'my-dialog': MyDialog,
    'bank-chooser': BankChooser,
    'check-order': CheckOrder
  },
  mounted () {
    this.buyNum = 1
    this.buyType = this.buyTypes[0]
    this.versions = [this.versionList[0]]
    this.period = this.periodList[0]
    this.getPrice()
  },
  methods: {
    onParamChange (attr, val) {
      this[attr] = val
      this.getPrice()
    },
    getPrice () {
      let buyVersionsArray = _.map(this.versions, (item) => {
        return item.value
      })
      let reqParams = {
        buyNumber: this.buyNum,
        buyType: this.buyType.value,
        period: this.period.value,
        version: buyVersionsArray.join(',')
      }
      axios.post('/api/getPrice', reqParams)
        .then((res) => {
          this.price = res.data.number
        })
    },
    showPayDialog () {
      this.isShowPayDialog = true
    },
    hidePayDialog () {
      this.isShowPayDialog = false
    },
    hideErrDialog () {
      this.isShowErrDialog = false
    },
    hideCheckOrder () {
      this.isShowCheckOrder = false
    },
    onChangeBanks (bankObj) {
      this.bankId = bankObj.id
    },
    confirmBuy () {
      let buyVersionsArray = _.map(this.versions, (item) => {
        return item.value
      })
      let reqParams = {
        buyNumber: this.buyNum,
        buyType: this.buyType.value,
        period: this.period.value,
        version: buyVersionsArray.join(','),
        bankId: this.bankId
      }
      axios.post('/api/createOrder', reqParams)
        .then((res) => {
          this.orderId = res.data.orderId
          this.isShowCheckOrder = true
          this.isShowPayDialog = false
        })
        .catch((err) => {
          console.log(err)
          this.isShowBuyDialog = false
          this.isShowErrDialog = true
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
  td,
  th{
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
  }
}
</style>



