<template lang="pug">
.detail-wrap
  .detail-left
    .product-board
      img(
        :src="productIcon"
      )
      ul
        router-link(
          v-for="item in products",
          :to="{path: item.path}",
          tag="li",
          active-class="active",
          :key="item.path"
        ) {{ item.name }}
  .detail-right
    keep-alive
      router-view
</template>
<script>
export default {
  data () {
    return {
      products: [
        {
          name: '',
          path: 'count',
          icon: require('../assets/images/1.png'),
          active: false
        },
        {
          name: '',
          path: 'forecast',
          active: false
        },
        {
          name: '',
          path: 'analysis',
          active: false
        },
        {
          name: '',
          path: 'publish',
          active: false
        }
      ],
      imgMap: {
        '/detail/count': require('../assets/images/1.png'),
        '/detail/forecast': require('../assets/images/2.png'),
        '/detail/analysis': require('../assets/images/3.png'),
        '/detail/publish': require('../assets/images/4.png')
      }
    }
  },
  computed: {
    productIcon () {
      return this.imgMap[this.$route.path]
    }
  }
}
</script>



