<template lang="pug">
.index-wrap
  .index-left
    .index-left-block
      h2 
      .product-list(
        v-for="product in productList"
      )
        h3 {{ product.title }}
        ul
          li(
            v-for="item in product.list"
          )
            a(
              :href="item.url"
            ) {{ item.name }}
            span.hot-tag(
              v-show="item.hot"
            ) HOT
        .hr(
          v-if="!product.last"
        )
    .index-left-block.lastest-news
      h2 
      ul
        li(
          v-for="item in newsList"
        )
          a.new-item(
            :href="item.url"
          ) {{ item.title }}
  .index-right
    slide-show(
      :slides="slides"
    )
    .index-board-list
      .index-board-item(
        v-for="(item, index) in boardList",
        :class="[{'line-last' : index % 2 !== 0}, 'index-board-' + item.id]"
      )
        .index-board-item-inner
          h2 {{ item.title }}
          p {{ item.description }}
          .index-board-button
            router-link.button(
              :to="{name: item.toKey}"
            ) 
</template>
<script>
import SlideShow from '@/components/base/slideShow'
import axios from 'axios'
export default {
  data () {
    return {
      productList: [],
      newsList: [],
      boardList: [],
      slides: []
    }
  },
  components: {
    'slide-show': SlideShow
  },
  mounted () {
    axios.get('api/getNewsList')
      .then((res) => {
        this.newsList = res.data.list
      })
      .catch((error) => {
        console.log(error)
      })
    axios.get('api/getBoardList')
      .then((res) => {
        this.boardList = res.data
      })
      .catch((error) => {
        console.log(error)
      })
    axios.get('api/getProductList')
      .then((res) => {
        this.productList = res.data
      })
      .catch((error) => {
        console.log(error)
      })
    axios.get('api/getSlides')
      .then((res) => {
        this.slides = res.data
      })
      .catch((error) => {
        console.log(error)
      })
  }
}
</script>



