<template lang="pug">
.sales-board
  .sales-board-intro
    h2 
    p 
  .sales-board-form
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right
        v-counter(
          :max="100",
          :min="20"
        )
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right
        v-mul-chooser(
          :selections="versionList"
        )
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right 
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right 500 
    .sales-board-line
      .sales-board-line-left &nbsp;
      .sales-board-line-right
        .button 
  .sales-board-des
    h2 
    p 2020

</template>
<script>
import VCounter from '@/components/base/counter'
import VMulChooser from '@/components/base/mulChooser'
export default {
  components: {
    'v-counter': VCounter,
    'v-mul-chooser': VMulChooser
  },
  data () {
    return {
      versionList: [
        {
          label: '',
          value: 0
        },
        {
          label: 'pdf',
          value: 1
        },
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 3
        }
      ]
    }
  }
}
</script>



