<template lang="pug">
.sales-board
  .sales-board-intro
    h2 
    p 
  .sales-board-form
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right
        v-counter(
          :max="100",
          :min="20"
        )
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right
        v-selection(
          :selections="tradeList"
        )
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right
        v-mul-chooser(
          :selections="tradeList"
        )
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right 
    .sales-board-line
      .sales-board-line-left 
      .sales-board-line-right 500
    .sales-board-line
      .sales-board-line-left &nbsp;
      .sales-board-line-right
        .button 
  .sales-board-des
    h2 
    p 
    table.sales-board-table
      tbody
        tr.ui-table-row
          td.col-first
            .intro-pic
              label 
          td
            .intro-pic
              label 
          td
            .intro-pic
              label 
          td
            .intro-pic
              label 
          td
            .intro-pic
              label 
          td
            .intro-pic
              label 
          td
            .intro-pic
              label 
        tr.ui-table-row
          td.col-first
            .intro-pic
              label 
          td.col-first
            .intro-pic
              label 
          td.col-first
            .intro-pic
              label 
          td.col-first
            .intro-pic
              label 
          td.col-first
            .intro-pic
              label 
          td.col-first
            .intro-pic
              label 
          td.col-first
            .intro-pic
              label 

</template>
<script>
import VSelection from '@/components/base/select'
import VCounter from '@/components/base/counter'
import VMulChooser from '@/components/base/mulChooser'
export default {
  components: {
    'v-counter': VCounter,
    'v-selection': VSelection,
    'v-mul-chooser': VMulChooser
  },
  data () {
    return {
      tradeList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 3
        },
        {
          label: '',
          value: 4
        }
      ],
      versionList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 3
        }
      ]
    }
  }
}
</script>



