<template lang="pug">
.login-form
  .g-form
    .g-form-line
      span.g-form-label 
      .g-form-input
        input(
          type="text",
          v-model="usernameModel",
          placeholder=""
        )
      span.g-form-error {{ userErrors.errorText }}
    .g-form-line
      span.g-form-label 
      .g-form-input
        input(
          type="password",
          v-model="passwordModel",
          placeholder=""
        )
      span.g-form-error {{ passwordErrors.errorText }}
      .g-form-line
        .g-form-btn
          a.button(
            @click="onLogin"
          ) 
      p {{ errorText }}
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      usernameModel: '',
      passwordModel: '',
      errorText: '',
      userFlag: false,
      passwordFlag: false
    }
  },
  computed: {
    userErrors () {
      console.log(this.usernameModel)
      let errorText, status
      if (!/@/g.test(this.usernameModel)) {
        status = false
        errorText = '@'
      } else {
        status = true
        errorText = ''
      }
      if (!this.userFlag) {
        errorText = ''
        // this.userFlag = true
      }
      return {
        status,
        errorText
      }
    },
    passwordErrors () {
      let errorText, status
      if (!/^\w{1,6}$/g.test(this.passwordModel)) {
        status = false
        errorText = '1-6'
      } else {
        status = true
        errorText = ''
      }
      if (!this.passwordFlag) {
        errorText = ''
        // this.passwordFlag = true
      }
      return {
        status,
        errorText
      }
    }
  },
  watch: {
    usernameModel (n, o) {
      console.log(n, o)
    }
  },
  methods: {
    onLogin () {
      if (!this.userErrors.status || !this.passwordErrors.status) {
        this.errorText = ''
      } else {
        this.errorText = ''
        axios.get('api/login')
          .then((res) => {
            this.$emit('has-log', res.data)
          }, (error) => {
            console.log(error)
          })
      }
    }
  }
}
</script>



